import React, { useEffect } from "react";

import Button from "../Components/Button";
import "../Styles/About.css";
import omlet from "../Images/omlet.jpg";
import Aos from "aos";
import "aos/dist/aos.css";

const About = () => {
  useEffect(() => {
    Aos.init({ duration: 3000 });
  }, []);
  return (
    <>
      <div id="about" className=" flex items-center justify-center pt-20">
        <p className="font-bold uppercase text-3xl">
          <span className="text-yellow-600">About</span> Us
        </p>
      </div>
      <div className="about">
        <div className="about__img">
          <img data-aos="fade-up" src={omlet} />
        </div>
        <div data-aos="fade-up" className="about__info">
          <h4 className="font-bold capitalize text-3xl text-yellow-500">
            What about our Foods
          </h4>
          <p>
            Excepteur eu ex culpa mollit minim. Labore ipsum dolore eu qui et
            exercitation eiusmod esse laborum anim. Excepteur irure amet sunt
            culpa cupidatat anim aute. In proident cillum aliqua anim sint
            cupidatat. Cupidatat aute proident commodo consequat tempor Lorem ad
            id reprehenderit amet consectetur pariatur Lorem ullamco. Anim
            dolore magna duis dolor magna laboris ea pariatur ad excepteur.
          </p>
          <Button text="Learn More" />
        </div>
      </div>
    </>
  );
};

export default About;
